:host {
  display: block;
  height: 100vh;
  width: 100vw;

  #map {
    height: 100vh;
    width: 100vw;
  }

  .marker {
    background-color: var(--ion-color-primary);
  }

  a {
    width: 40px;
    height: 40px;
  }
  .fab {
    position: absolute;
    top: calc(var(--ion-safe-area-top) + 100px);
    display: flex;
    flex-direction: column;
    width: 40px;
    height: 40px;

    &-left {
      left: 10px;
    }

    &-right {
      right: 5px;

      .second-fab {
        top: 60px;
      }
      .third-fab {
        top: 108px;
      }
      .fourth-fab {
        top: 156px;
      }
    }
  }
  ion-fab-list {
    gap: 10px;
    ion-fab-button {
      //Need to force this because the wrapping <a> tag remove the automatic width and height from fab-list
      width: 40px;
      height: 40px;
    }
  }
  ion-spinner {
    width: 15px;
    height: 15px;
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
